@import url(https://fonts.googleapis.com/css?family=Pacifico:700|Open+Sans:300,400);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale; }

.gnome__list {
  list-style-type: none;
  display: grid;
  -webkit-justify-content: center;
          justify-content: center;
  grid-template-columns: repeat(3, 25%);
  grid-gap: 30px;
  font-family: 'Open Sans', sans-serif; }

.gnome {
  background-color: #FFF8EE;
  border-radius: 5%;
  width: 500px;
  height: 300px; }
  .gnome:hover {
    box-shadow: 0 14px 28px rgba(0, 0, 0, 0.25), 0 10px 10px rgba(0, 0, 0, 0.22); }

.glowButton {
  height: 300px;
  padding: 0 14px;
  transition: all .3s ease; }

.glowButton:hover {
  background: #FFC75F;
  border-color: #FFC75F;
  box-shadow: 0 0 25px 1px white; }

.gnome__image {
  width: 200px;
  height: 200px;
  float: right; }

.gnome__details {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-around;
          justify-content: space-around;
  margin-left: 5px;
  margin-bottom: 0px; }

.gnome__lists {
  display: -webkit-flex;
  display: flex; }

.app__filter-item {
  background: #414141;
  padding: 5px; }

.app__filter-fullname {
  border: 0;
  padding: 10px;
  width: 100%;
  box-sizing: border-box;
  font-weight: bold;
  font-size: 18px;
  outline: none;
  text-align: center; }

.app__header {
  text-align: center;
  font-size: 40px; }

.App {
  background-color: #A68D65; }

.gnome__name {
  text-align: right;
  margin-right: 10px; }

